import React from "react";
import { Col, Row } from "react-bootstrap";
import { HeaderContent, Progress } from "../../components";
import "./Skil.scss";

const Skill = () => {
  return (
    <Row>
      <Col md={{ span: 10, offset: 1 }}>
        <HeaderContent title="My Skill" />
        <Row>
          <Col md>
            <Progress label="HTML" value={85} variant="warning" />
            <Progress label="CSS3" value={85} variant="warning" />
            <Progress label="Jquery" value={80} variant="warning" />
            <Progress label="ExtJS" value={80} variant="warning" />
            <Progress label="MySql" value={85} variant="warning" />
          </Col>
          <Col md>
            <Progress label="ReactJS" value={70} variant="warning" />
            <Progress label="React Native" value={70} variant="warning" />
            <Progress label="Bootstrap" value={80} variant="warning" />
            <Progress label="PHP (CI & Laravel)" value={85} variant="warning" />
            <Progress label="Oracle" value={85} variant="warning" />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Skill;
