import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  ICCalender,
  ICEmail,
  ICGlobe,
  ICHome,
  ICPhone,
  ICUser,
} from "../../assets";
import { HeaderContent, ListItem } from "../../components";
import "./AboutMe.scss";

const AboutMe = () => {
  return (
    <Row>
      <Col>
        <HeaderContent title="About Me" />
        <Row>
          <Col md={{ span: 10, offset: 1 }}>
            <ListItem label="Name" value="Derian Indra Pratama" icon={ICUser} />
            <ListItem
              label="Date of birth"
              value="07 April 1993"
              icon={ICCalender}
            />
            <ListItem label="Phone" value="+62 858 6172 8801" icon={ICPhone} />
            <ListItem
              label="Email"
              value="derian.indra@outlook.com"
              icon={ICEmail}
            />
            <ListItem label="Web" value="derianip.github.io" icon={ICGlobe} />
            <ListItem
              label="Address"
              value="Jakarta, Indonesia"
              icon={ICHome}
            />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default AboutMe;
