import React, { useEffect, useState } from "react";
import { Col, Image, Row, Tab, Tabs } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  ICCss,
  ICExpress,
  ICExtjs,
  ICHtml,
  ICPhp,
  ICReactjs,
  ICSass,
  imgBilling,
  imgNpks,
  imgResume,
  imgMystore,
  ICBootstrap,
} from "../../assets";
import { HeaderContent, ModalRJ } from "../../components";
import { setDataPortfolio } from "../../redux/action";
import "./Portfolio.scss";

const Portfolio = () => {
  const [show, setShow] = useState({ isOpen: false });
  const { data } = useSelector((state) => state.portfolioReducer);
  const dispacth = useDispatch();

  useEffect(() => {
    const arrData = [
      {
        title: "NPK-S",
        desc:
          "An operating system that handles container activities on line 2, including stuffing and stripping activities, monitoring yard & container movement which is integrated with the new billing system (NBS) payment process.",
        role: "FullStack",
        techno: [ICHtml, ICCss, ICSass, ICPhp, ICExpress, ICExtjs],
        image: imgNpks,
      },
      {
        title: "NPK-S Billing",
        desc:
          "Payment system for non-container stuffing stripping (NPKS) activities including receiving delivery container & container movement costs,this system is integrated with the NPKS Terminal operating system.",
        role: "Front End Developer",
        techno: [ICHtml, ICCss, ICSass, ICPhp, ICExpress, ICExtjs],
        image: imgBilling,
      },
      {
        title: "Resume",
        desc: "derianip.github.io",
        techno: [ICHtml, ICCss, ICBootstrap, ICSass, ICReactjs],
        image: imgResume,
      },
      {
        title: "My Store",
        desc: "derianip.github.io/my-store",
        techno: [ICHtml, ICCss, ICBootstrap, ICSass, ICReactjs],
        image: imgMystore,
      },
    ];
    dispacth(setDataPortfolio(arrData));
  }, [dispacth]);

  const modalHandler = (id, isShow) => {
    setShow({
      isOpen: {
        [id]: isShow,
      },
    });
  };

  return (
    <Row className="portfolio-wrapper">
      <Col md={{ span: 10, offset: 1 }}>
        <HeaderContent title="My Portfolio" />
        <Row>
          <Col className="text-center mb-3">
            <Tabs variant="pills" className="d-flex justify-content-center">
              <Tab eventKey="All" title="All" tabClassName="mb-5 tab-text">
                <Row className="content">
                  {data.map((item, i) => {
                    return (
                      <Col xs={6} md={4} className="mb-3" key={i}>
                        <Image
                          src={item.image}
                          alt={item.title}
                          fluid
                          className="thumb"
                        />
                        <span onClick={() => modalHandler(i, true)} />
                        <ModalRJ
                          show={show.isOpen[i]}
                          handleClose={() => modalHandler(i, false)}
                          title={item.title}
                          desc={item.desc}
                          role={item.role}
                          image={item.image}
                          techno={item.techno}
                        />
                      </Col>
                    );
                  })}
                </Row>
              </Tab>
              <Tab eventKey="web" title="Web" tabClassName="mb-5 tab-text">
                <Row className="content">
                  {data.map((item, i) => {
                    return (
                      <Col xs={6} md={4} className="mb-3" key={i}>
                        <Image
                          src={item.image}
                          alt={item.title}
                          fluid
                          className="thumb"
                        />
                        <span onClick={() => modalHandler(i, true)} />
                        <ModalRJ
                          show={show.isOpen[i]}
                          handleClose={() => modalHandler(i, false)}
                          title={item.title}
                          desc={item.desc}
                          role={item.role}
                          image={item.image}
                          techno={item.techno}
                        />
                      </Col>
                    );
                  })}
                </Row>
              </Tab>
              <Tab
                eventKey="mobile"
                title="Mobile"
                tabClassName="mb-5 tab-text"
              >
                <Row className="content">
                  <Col xs={6} md={4} className="mb-3">
                    {/* <Image src={imgNpks} alt="npks" fluid className="thumb" /> */}
                  </Col>
                </Row>
              </Tab>
            </Tabs>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Portfolio;
