import React from "react";
import { Col, Image, Row } from "react-bootstrap";
import {
  ICFacebook,
  ICGithub,
  ICInstagram,
  ICLinkedin,
  ICTwitter,
} from "../../assets";
import { HeaderContent } from "../../components";
import "./Contact.scss";

const Contact = () => {
  return (
    <Row className="contact-wrapper">
      <Col xs={{ span: 10, offset: 1 }}>
        <HeaderContent title="My Contact" />
        <Row>
          <Col className="text-center">
            <p>Tanjung Priuk - Jakarta Utara, Indonesia</p>
            <p>+62 858 6172 8801 - derian.indra@outlook.com</p>
          </Col>
        </Row>
        <Row className="d-flex justify-content-center mt-2">
          <Col xs={2} className="text-center">
            <a href="http://facebok.com/derian.7" target="blank_">
              <Image
                src={ICFacebook}
                width={32}
                height={32}
                className="img-fluid"
              />
            </a>
          </Col>
          <Col xs={2} className="text-center">
            <a href="http://twitter.com/derianip" target="blank_">
              <Image
                src={ICTwitter}
                width={32}
                height={32}
                className="img-fluid"
              />
            </a>
          </Col>
          <Col xs={2} className="text-center">
            <a href="http://github.com/derianip" target="blank_">
              <Image
                src={ICGithub}
                width={32}
                height={32}
                className="img-fluid"
              />
            </a>
          </Col>
          <Col xs={2} className="text-center">
            <a href="http://instagram.com/derian.pratama" target="blank_">
              <Image
                src={ICInstagram}
                width={32}
                height={32}
                className="img-fluid"
              />
            </a>
          </Col>
          <Col xs={2} className="text-center">
            <a href="https://www.linkedin.com/in/derianip/" target="blank_">
              <Image
                src={ICLinkedin}
                width={32}
                height={32}
                className="img-fluid"
              />
            </a>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Contact;
